* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

body {
    font-family: Arial, sans-serif;
    line-height: 1.6;
    background-color: #000;
    color: #fff;
}

footer {
    background-color: #000;
    padding: 20px 0;
    text-align: center;
}

footer h4 {
    margin-bottom: 10px;
    font-size: 1.2rem;
}

footer a {
    color: #fff;
    text-decoration: none;
    margin: 0 10px;
    transition: color 0.3s, text-shadow 0.3s;
}

footer a:hover {
    color: #00ff00;
    text-shadow: 0 0 5px #00ff00;
}

.social-icons {
    margin: 20px 0;
}

.social-icons a {
    margin: 0 15px;
    display: inline-block;
    font-size: 1.5rem;
    transition: transform 0.3s, color 0.3s;
}

.social-icons a:hover {
    transform: scale(1.2);
    color: #00ff00;
}

.footer-bottom {
    margin-top: 20px;
    font-size: 0.9rem;
    color: #aaa;
}
