body {
  margin: 0;
  font-family: Arial, sans-serif;
  background-color: transparent; /* Set background to white */
  position: sticky;
}

.navbar {
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  padding: 20px 20px;
  background-color: black; /* Set navbar background to white */
  border-bottom: 1px solid #C79836; /* Optional: light border for separation */
}


.nav-links {
  list-style: none;
  display: flex;
  gap: 100px;
}

.nav-links li a {
  text-decoration: none;
  color: rgb(255, 255, 255); /* Set text to black */
  font-size: 18px;
  transition: color 0.3s ease-in-out;
  position: relative;
}

/* Mustard design: underlining effect */
.nav-links li a::after {
  content: '';
  position: absolute;
  width: 0;
  height: 2px;
  background-color: #C79836; /* Mustard color */
  left: 50%;
  bottom: -4px;
  transition: all 0.3s ease-in-out;
}

.nav-links li a:hover::after {
  width: 100%;
  left: 0;
}

.nav-links li a:hover {
  color: #C79836; /* Change text color to mustard on hover */
}

@media screen and (max-width: 678px) {
 .nav-links{
  gap: 25px;
 } 
  
}