
.home-section {
  padding: 50px;
  text-align: center;
  /* background-color: white;  */
  background-image: url("../MBG.jpg");
  height: 650px;
  align-items: center;
  display: flex;
}

.home-section .content {
  max-width: 800px;
  margin: auto;
}

.home-section h1 {
  font-size: 3rem;
  margin-bottom: 20px;
  color: rgb(255, 255, 255); /* Set text color to black */
}

.home-section p {
  font-size: 1.2rem;
  line-height: 1.8;
  color: rgb(255, 255, 255); /* Set text color to black */
}

.highlight {
  color: #C79836; /* Mustard color for highlight */
  font-weight: bold;
}

.logo{
  width: 15vb;
  height: 10vh;
}

