.about-section {
    padding: 50px;
    text-align: center;
    /* background-color: black; */
    background-image: url("../MBG.jpg");
    height: 650px;
    align-items: center;
    display: flex;
  }
  
  .about-section .content {
    max-width: 800px;
    margin: auto;
  }
  
  .about-section h2 {
    font-size: 2.5rem;
    margin-bottom: 20px;
    color: white;
  }
  
  .about-section p {
    font-size: 1.2rem;
    line-height: 1.8;
    color: white;
  }
  
  .highlight {
    color: #C79836;
    font-weight: bold;
  }
  